import axios from 'axios'
import {MallUrl, IndexUrl} from '../config/urls'

/**
 * @desc 
 * */
export const getSuggest = function () {
  return new Promise((resolve, reject) => {
    axios.get(MallUrl.suggest)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * @desc 
 * @param {int} page 
 * @param {string} keywords 
 * @param {string} labels label
 * @return {Promise}
 * */
export const getMallProductList = function (page, keywords, labels) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.mallProductList, {page: page, keywords: keywords, labels: labels})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

