// 
export const uploadFaPiao = (file) => {
  return new Promise((resolve, reject) => {
    axios.post(MaintainUrl.uploadVerification, file, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

